
import { defineComponent, ref, onMounted, Ref, computed, reactive } from 'vue'
// import { UserOutlined } from '@ant-design/icons-vue'
import { GlobalDataProps } from '../store/index'
import StyledUploader from '../components/StyledUploader.vue'
import { useStore } from 'vuex'
import { message } from 'ant-design-vue'
import { UploadImgProps } from '../helper'
export interface RuleFormInstance {
  validate: () => Promise<any>;
}
interface PaginationProps {
  pageSize: number;
  total: number;
  current: number;
}
export default defineComponent({
  name: 'Setting',
  components: {
    // UserOutlined,
    // StyledUploader
  },
  setup () {
    const store = useStore<GlobalDataProps>()
    const publishForm = ref() as Ref<RuleFormInstance>
    const user = computed(() => store.state.user.data)
    const works = computed(() => store.state.works.works)
    const loading = computed(() => store.state.status.loading)
    const currentKey = ref('profile')
    const pagination = reactive<PaginationProps>({
      pageSize: 8,
      total: 0,
      current: 1
    })
    const form = reactive({
      username: user.value.nickName || '',
      gender: user.value.gender || '0',
      uploaded: { data: { url: (user.value.picture || 'https://poster-master.oss-cn-guangzhou.aliyuncs.com/upload-files/logo-profile.png') } }
    })
    const status = computed(() => store.state.status)
    const tableColumns = [
      {
        title: '',
        dataIndex: 'title',
        key: 'title'
      },
      {
        title: 'ID',
        dataIndex: 'id',
        key: 'id'
      },
      {
        title: '',
        dataIndex: 'updatedAt',
        key: 'updatedAt'
      },
      {
        title: '',
        key: 'action',
        slots: { customRender: 'action' }
      }
    ]
    const rules = {
      username: [
        { required: true, message: '', trigger: 'blur' }
      ]
    }
    const updateAvatar = (rawData: UploadImgProps) => {
      form.uploaded = {
        data: { url: rawData.data.urls[0] }
      }
    }
    onMounted(() => {
      store.dispatch('fetchWorks', { pageIndex: 0, pageSize: 8, status: 0 }).then(({ data }) => {
        pagination.total = data.count
      })
    })
    const recoverWork = (id: number) => {
      store.dispatch('recoverWork', id).then(() => {
        message.success('', 1)
      })
    }
    const handleTableChange = (pager: PaginationProps) => {
      pagination.current = pager.current
      store.dispatch('fetchWorks', { pageIndex: pager.current - 1, pageSize: 8, status: 0 })
    }
    const update = () => {
      publishForm.value.validate().then(() => {
        const payload = {
          nickName: form.username,
          gender: parseInt(form.gender),
          picture: form.uploaded.data.url
        }
        store.dispatch('updateUserAndFetch', payload).then(() => {
          message.success('', 1)
        })
      })
    }
    return {
      form,
      status,
      rules,
      update,
      updateAvatar,
      publishForm,
      works,
      tableColumns,
      recoverWork,
      pagination,
      handleTableChange,
      loading,
      currentKey
    }
  }
})
